<template>
    
    <div v-if="index > 0">
        <div v-for="k in indexList" :key="k">
            <div v-for="(v, i) in childs" :key="i">
                
                    <div class="qiong-td qiong-pb-7 qiong-pt-5 qiong-txt-td" 
                        v-if="k == v.index" 
                        :class="{ 'qiong-td-zebra': (v.index - me.index) % 2 != 0, 'qiong-td-light': (v.index - me.index) % 2 == 0 }"
                    >
                        <div class="qiong-wide-9"></div>
                        <div class="qiong-wide-11">{{ v.username }}</div>
                        <div class="qiong-wide-9">{{ v.number_code }}</div>
                        <div class="qiong-wide-30">{{ v.display_name }}</div>
                        <div class="qiong-wide-6">{{ v.index - me.index }}</div>
                        <div class="qiong-wide-6">{{ v.ipv }}</div>
                        <div class="qiong-wide-8">
                            <level-viewing :item="v.vip.name + '_' + v.vip.star"></level-viewing>
                        </div> 
                        <div class="qiong-wide-8">{{ v.ipv_first }}</div>
                        <div class="qiong-wide-8">{{ v.bonus.usd_recommend }}</div>
                        <div class="qiong-wide-5">
                            <qiong-icon-inset :color="'primary'" :arg="v" @done_Father="openCenter"></qiong-icon-inset>
                        </div>
                    </div>
                    
            </div>
        </div>
    </div>

    <qiong-empty v-else></qiong-empty>

</template>

<script>
import Loading from '../../../../../components/Data/Loading.vue'
import QiongIconInset from '../../../../../components/Qiong/Button/QiongIconInset.vue'
import QiongEmpty from '../../../../../components/Qiong/Ui/QiongEmpty.vue'
import LevelViewing from '../../../../../components/Viewing/LevelViewing.vue'
    export default {
  components: { LevelViewing, QiongIconInset, Loading, QiongEmpty },
        inject: [ 'reload' ],
        props: {
            index: {
                type: Number
            },
            me: {
                type: Object
            },
            childs: {
                type: Array
            }
        },
        name: '',
        mounted() {
            this.indexList = [ ]

            for (let k= 0; k< this.index; k++ ) {
                this.indexList.push(k + 1 + this.me.index)
            }
        },
        data() {
            return {
                indexList: [ ]
            }
        },
        methods: {
            
            openCenter(user) {
                // this.$store.commit('saveUser', user)

                this.$router.push({ path: '/home/bonus/recommend_one/', query: { 'id': user.unid } })
                this.reload()
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


