<template>
    <qiong-header-bonus-one>

        <div slot="header" v-if="me" class="qiong-txt-primary qiong-txt-shadow">
            {{ me.username }}
        </div>
        <qiong-loading v-else slot="header"></qiong-loading>

        <div slot="content" class="qiong-td" v-if="me">
            <div class="qiong-wide-15 qiong-txt-tr fw-b">
                用戶編號&nbsp;:&nbsp;
                <span class="qiong-txt-td fw-n">{{ me.number_code }}</span>
            </div>
            <div class="qiong-wide-34 qiong-txt-tr fw-b">
                用戶全稱&nbsp;:&nbsp;
                <span class="qiong-txt-td fw-n">{{ me.display_name }}</span>
            </div>

            <div class="qiong-wide-9 qiong-txt-tr fw-b">
                IPV&nbsp;:&nbsp;
                <span class="qiong-txt-td fw-n">{{ me.ipv }}</span>
            </div>
            <div class="qiong-wide-15 qiong-txt-tr fw-b">
                支付級別&nbsp;:&nbsp;
                <div class="qiong-txt-td fw-n d-inline-block">
                    <level-viewing :item="me.vip.name + '_' + me.vip.star "></level-viewing>
                </div>
            </div>

            <div class="qiong-wide-13 qiong-txt-tr fw-b">
                首購的PV&nbsp;:&nbsp;
                <span class="qiong-txt-td fw-n">{{ me.ipv_first }}</span>
            </div>
            <div class="qiong-wide-14 qiong-txt-tr fw-b">
                推薦獎金&nbsp;:&nbsp;
                <span class="qiong-txt-td fw-n">{{ me.bonus.usd_recommend }}</span>
            </div>
        </div>
        <qiong-loading v-else slot="header" :bigger="-1"></qiong-loading>
    </qiong-header-bonus-one>
</template>

<script>
import Loading from '../../../../../components/Data/Loading.vue'
import QiongHeaderBonusOne from '../../../../../components/Qiong/Pager/QiongHeaderBonusOne.vue'
import QiongLoadingDiv from '../../../../../components/Qiong/QiongLoadingDiv.vue'
import QiongLoading from '../../../../../components/Qiong/Ui/QiongLoading.vue'
import LevelViewing from '../../../../../components/Viewing/LevelViewing.vue'
    export default {
  components: { QiongHeaderBonusOne, LevelViewing, Loading, QiongLoadingDiv, QiongLoading },
        props: {
            me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


